*{
    padding:0;
    margin:0;
    box-sizing: border-box;
}
.content{
    height:100vh;
}
.navbar{
    background-color: darkblue;
}
.register-page{
    display: flex;
    align-items: center;
    justify-content: center;
    height:90vh;
}
.button{

    padding:15px 20px;
    margin-right: 200px;
    display: block;
    justify-content: space-between;
}
.filters{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:15px 20px;
    box-shadow: 0 0 3px darkcyan;
}
.anticon svg{
    font-size:20px;
    cursor:pointer;
color:black
}
.active-icon{
    color: white;
}
.inactive-icon{
    color:white;
}
.switch-icon{
    box-shadow: 0 0 15px red;
    border-radius: 5px;
    padding: 10px 14px;
}