.register-page{
    background-image: url("https://png.pngtree.com/thumb_back/fh260/back_our/20190620/ourmid/pngtree-cat-yellow-geometric-taobao-main-map-image_172724.jpg");
    
    background-size: cover;
    background-position: center;
    width: 100vw;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.register-page {
  height: 100vh; /* Set the height of the container to 100% of the viewport height */
  background-color: #282c34; /* Set the background color as needed */
}

/* Add margin to the form to create space on both sides */
.register-page Form {
  width: 440px;
  height:500px; /* Set the width of the form as needed */
  margin: 0 auto; /* Center the form horizontally */
  padding: 20px; /* Add padding to the form */
}

/* Style the login button */
.register-page button {
  width: 100%; /* Make the button fill the width of its container */
  height: 50px;
  border: 2px solid white;
  margin-top: 20px; /* Add margin at the top of the button */
}
